/* General Container */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* Main Content */
.mainContent {
  padding: 24px;
}

/* Header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  border-bottom: 2px solid #eee;
}

.header h1 {
  font-size: 1.8rem;
  font-weight: 600;
}

.toggleButtons {
  display: flex;
  gap: 10px;
}

/* Toggle Button Styles */
.toggleButton {
  padding: 8px 20px;
  background: #4f46e5;
  cursor: pointer;
  margin: 5px;
  color: #fff;
  border-radius: 18px;
  border: none;
}

/* Product Grid */
.productGrid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

/* Product Card */
.productCard {
  padding: 16px;
  border: 1px solid #ddd;
  border-radius: 8px;
  transition: transform 0.3s ease-in-out;
  cursor: pointer;
  background: #ffffff;
}

.productCard:hover {
  background: #ddd;
}

/* Product Image */
.productImage {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
}

/* Product Info */
.productInfo {
  margin-top: 10px;
}

.strikeThrough {
  text-decoration: line-through;
  color: #888;
}

/* Modal */
.modal {
  position: fixed;
  top: 0;
  left: -100%;
  width: 250px;
  height: 100%;
  background-color: #fff;
  box-shadow: 4px 0px 10px rgba(0, 0, 0, 0.1);
  transition: left 0.3s ease-in-out;
  z-index: 999;
}

.modal.open {
  left: 0;
}

.modalContent {
  padding: 20px;
}

/* Close Modal Button */
.closeModal {
  background-color: transparent; /* Clear background */
  color: #333; /* Dark text color */
  font-size: 1.5rem;
  font-weight: bold;
  border: none;
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 8px 12px;
  border-radius: 50%; /* Round button */
  transition: background-color 0.2s, color 0.2s;
  outline: none;
}

.closeModal:hover {
  background-color: #f4f4f4; /* Subtle background change */
  color: #4f46e5; /* Change text color on hover */
}

.closeModal:active {
  transform: scale(0.98); /* Slight compression for click */
}

.closeModal:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(79, 70, 229, 0.2); /* Subtle glow on focus */
}

/* Filter & Sort */
.filters,
.sort {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.filterSection,
.sort button {
  display: flex;
  flex-direction: column;
}

.filterOption,
.sort button {
  display: flex;
  align-items: center;
  gap: 8px;
}

.filterOption input,
.sort button {
  margin-right: 10px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.filterOption input:checked {
  background-color: #4f46e5;
  border-color: #4f46e5;
}

.sort button:hover {
  background-color: #4f46e5;
  color: white;
}

/* Pagination */
.pagination {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
}

.pagination button {
  background-color: #4f46e5;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.pagination button:hover {
  background-color: #4338ca;
}

/* Responsive Grid */
@media (max-width: 768px) {
  .productGrid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 480px) {
  .productGrid {
    grid-template-columns: 1fr;
  }
  .header h1 {
    font-size: 1.2rem;
  }
}

/* Checkbox Container */
.filterOption {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 10px;
}

/* Custom Checkbox */
.filterOption input[type="checkbox"] {
  appearance: none;
  width: 20px;
  height: 20px;
  border: 2px solid #ddd;
  border-radius: 50%;
  background-color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
}

.filterOption input[type="checkbox"]:checked {
  background-color: #4f46e5;
  border-color: #4f46e5;
}

.filterOption input[type="checkbox"]:checked::before {
  content: "✔";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 12px;
}

.filterOption input[type="checkbox"]:hover {
  border-color: #4f46e5;
}

.filterOption input[type="checkbox"]:focus {
  outline: none;
  border-color: #4f46e5;
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.2);
}

.filterOption label {
  font-size: 1rem;
  cursor: pointer;
}

/* Sort Button */
.sort button {
  padding: 10px 20px;
  background-color: white;
  border: 2px solid #ddd;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  margin: 5px;
  font-weight: 500;
  position: relative;
  min-width: 160px;
}

.sort button:hover {
  border-color: #4f46e5;
  background-color: #f4f4f4;
}

.sort button.selected {
  background-color: #4f46e5;
  color: white;
  border-color: #4f46e5;
}

.sort button:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.2);
}

.sort button::before {
  content: "✔";
  position: absolute;
  left: 10px;
  font-size: 16px;
  color: #fff;
  display: none;
}

.sort button.selected::before {
  display: inline-block;
}

.productLink {
  text-decoration: none;
  color: #333;
}

.productLink:visited {
  color: #333; /* or choose a color you prefer */
}

.modalContent {
  /* background: red; */
}

.filterOptions {
  /* background: yellow; */
  max-height: 70vh; /* Adjust as needed */
  overflow-y: auto;
  /* scrollbar-width: none;  */
}

.filterOptions::-webkit-scrollbar {
  width: 6px; /* Adjust width */
}

.filterOptions::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

.filterOptions::-webkit-scrollbar-thumb {
  background: #5f74d1;
  border-radius: 10px;
}
