.container {
  max-width: 80rem;
  margin: auto;
  padding: 2rem;
  background-color: #ffffff;
  margin-top: 3rem;
  border-radius: 6px;
  border: 1px solid rgb(209, 209, 209);
}

.title {
  font-size: 2rem;
  font-weight: bold;
  color: #1f2937;
  margin-bottom: 1.5rem;
}

.list {
  list-style: none;
  padding: 0;
}

.listItem {
  display: flex;
  padding: 1rem 0;
  border-bottom: 1px solid #e5e7eb;
}

.imageContainer {
  width: 6rem;
  height: 6rem;
  flex-shrink: 0;
  overflow: hidden;
  border-radius: 0.375rem;
  border: 1px solid #e5e7eb;
}

.image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.details {
  flex: 1;
  margin-left: 1rem;
  display: flex;
  flex-direction: column;
}

.productTitle {
  font-size: 1rem;
  font-weight: 500;
  color: #1f2937;
  text-decoration: none;
}

.productBrand {
  font-size: 0.875rem;
  color: #6b7280;
  margin-top: 0.25rem;
}

.productPrice {
  font-size: 1rem;
  font-weight: 500;
  color: #1f2937;
}

.quantityContainer {
  display: flex;
  align-items: center;
  font-size: 0.875rem;
  color: #6b7280;
}

.quantitySelect {
  margin-left: 0.5rem;
  padding: 0.25rem;
  width: 50px;
}

.removeButton {
  font-size: 0.875rem;
  font-weight: 500;
  color: #4f46e5;
  background: none;
  border: none;
  cursor: pointer;
  margin-left: auto;
}

.removeButton:hover {
  color: #4338ca;
}

.summary {
  border-top: 1px solid #e5e7eb;
  padding-top: 1rem;
  margin-top: 1rem;
}

.summaryItem {
  display: flex;
  justify-content: space-between;
  font-size: 1rem;
  font-weight: 500;
  color: #1f2937;
  margin-bottom: 0.5rem;
}

.infoText {
  font-size: 0.875rem;
  color: #6b7280;
}

.checkoutButton {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #4f46e5;
  color: white;
  font-size: 1rem;
  font-weight: 500;
  padding: 0.75rem;
  border-radius: 0.375rem;
  text-decoration: none;
  text-align: center;
  margin-top: 1rem;
  border: none;
  cursor: pointer;
}

.checkoutButton:hover {
  background-color: #4338ca;
}

.continueShopping {
  text-align: center;
  font-size: 0.875rem;
  color: #6b7280;
  margin-top: 1rem;
}

.continueShopping a {
  font-weight: 500;
  color: #4f46e5;
  text-decoration: none;
}

.continueShopping a:hover {
  color: #4338ca;
}



/* Style for the empty cart message */
.emptyCartMessage {
  text-align: center;
  font-size: 1.25rem;
  color: #4b5563; /* Gray-600 color */
  padding: 2rem 1rem;
}

.emptyCartMessage p {
  font-weight: 600;
}

.emptyCartMessage a {
  color: #4f46e5;  /* Indigo-600 color */
  text-decoration: underline;
}

.emptyCartMessage a:hover {
  color: #4338ca;  /* Indigo-500 color */
}


@media screen and (max-width: 768px) {
  .container {
    width: 98vw;
  }
  

}
