/* PageNotFound.module.css */
.container {
  display: grid;
  place-items: center;
  min-height: 100vh;
  background-color: white;
  padding: 6rem 1.5rem;
}

.textCenter {
  text-align: center;
}

.errorCode {
  font-size: 1rem;
  font-weight: 600;
  color: #4f46e5; /* Indigo color */
}

.title {
  margin-top: 1rem;
  font-size: 2rem;
  font-weight: 700;
  color: #1f2937; /* Gray-900 color */
  letter-spacing: -0.01562rem;
}

.description {
  margin-top: 1.5rem;
  font-size: 1rem;
  color: #4b5563; /* Gray-600 color */
}

.buttonContainer {
  margin-top: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;
}

.button {
  border-radius: 0.375rem; 
  background-color: #4f46e5; 
  padding: 0.625rem 1rem;
  font-size: 0.875rem;
  font-weight: 600;
  color: white;
  cursor: pointer;
}

.button:hover {
  background-color: #4338ca; 
}
