/* Navbar container */
.navbar {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #ffffff;
  border: 1px solid #c2cbd5;
  border-radius: 8px;
  height: 70px;
}

.navbar a {
  text-decoration: none; /* Ensure no underlines */
  color: #646363;
}

/* Logo */
.logo a {
  text-decoration: none;
  color: #646363;
  font-size: 1.5rem;
  font-weight: bold;
  letter-spacing: 1px;
  transition: color 0.3s;
  margin-right: 0.5em;
}

.logo a:hover {
  color: #fbbf24;
}

/* Navigation Links */
.navList {
  display: flex;
  list-style: none;
  gap: 1.5rem;
}

.navlinks {
  text-decoration: none;
  color: #646363;
  transition: color 0.3s, transform 0.2s;
}

.navlinks.active {
  color: #fbbf24;
  font-weight: bold;
  text-decoration: none;
}

.navlinks:hover {
  color: #fbbf24;
  transform: translateY(-2px);
}

.cartAndUser {
  display: flex;

  align-items: center;
  justify-content: space-between;
  width: 6rem;
}

.cart {
  display: inline-block;
  width: 40px;
}

.user {
  display: inline-block;
  width: 40px;
  position: relative;
}

.cartIcon {
  font-size: 1.5rem;
  color: #646363;
  transition: color 0.3s;
  text-decoration: none;
}

.cartIcon:hover {
  color: #fbbf24;
}

.cartBadge {
  position: absolute;
  top: 5px;
  /* right: 25px; */
  background: red;
  color: #fff;
  font-size: 0.875rem;
  padding: 0.25rem 0.5rem;
  border-radius: 50%;
  margin-left: -10px;
}

.userButton {
  border: none;
  background: none;
  color: #646363;
  cursor: pointer;
  font-size: 1.5rem;
  transition: color 0.3s;
}

.userButton:hover {
  color: #fbbf24;
}

.dropdownMenu {
  position: absolute;
  top: 100%;
  right: 0;
  background-color: white;
  border: 1px solid #c2cbd5;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  width: 200px; /* Set fixed width for the dropdown */
  min-width: 200px; /* Ensure it does not shrink */
  z-index: 1000;
  padding: 0; /* Remove extra padding */
  box-sizing: border-box; /* Ensure padding does not affect the width */
}

.dropdownItem {
  text-decoration: none;
  color: #646363;
  padding: 1rem;
  display: block;
  transition: background-color 0.3s, color 0.3s;
}

.dropdownItem:hover {
  background-color: #fbbf24;
  color: white;
}

/* Hamburger Button (Mobile) */
.hamburger {
  display: none;
  font-size: 1.5rem;
  background: none;
  border: none;
  color: #646363;
  cursor: pointer;
  transition: transform 0.3s ease;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .hamburger {
    display: block;
  }

  .navList {
    position: fixed;
    left: -100%;
    width: 50%;
    height: 100%;
    top: 0;
    background: #ffffff;
    flex-direction: column;
    padding: 2rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
    border: 1px solid #c2cbd5;
  }

  .mainDiv {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5px;
  }

  .navbar {
    display: flex;
    box-sizing: border-box;

    justify-content: center;
    align-items: center;
    width: 12rem;
    height: 62px;
    /* padding: 0px 3px; */
  }
  .webName {
    font-size: 18px;
  }
  .showMenu {
    left: 0;
  }

  /* Active hamburger button color */
  .hamburger.active {
    color: #fbbf24;
  }

  .cartAndUser {
    position: absolute;
    right: 0;
  }

  .cartBadge {
    top: -10px;
    right: 55px;
  }
}

/* Mobile Styles */
@media (max-width: 480px) {
  .logo {
    display: inline-block;
    box-sizing: border-box;
    padding: 0px 2px;
  }

  .navbar {
    width: 10rem;
    box-sizing: border-box;
  }
  .webName {
    font-size: 13px;
  }
}
