/* General Container Styles */
.container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 50px 15px;
  max-width: 1200px;
  margin: 0 auto;
}

/* Loading Styles */
.loading {
  font-size: 1.4rem;
  color: #333;
  text-align: center;
  margin-top: 50px;
  font-weight: 500;
}

/* Product Details Section */
.productDetail {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  width: 100%;
  background-color: #fff;
  border-radius: 15px;
  padding: 40px;
  max-width: 1000px;
}

/* Product Images */
.productImages {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.mainImage {
  width: 100%;
  height: auto;
  max-width: 400px;
  border-radius: 12px;
  cursor: pointer;
}
.mainImage:hover {
  transform: scale(1.01);
}

.imageGrid {
  display: flex;
  justify-content: space-between;
  gap: 5px;
  margin-top: 20px;
}

.gridImage {
  width: 30%;
  height: auto;
  border-radius: 12px;
  object-fit: cover;
  cursor: pointer;
}

.gridImage:hover {
  transform: scale(1.01);
}

/* Product Info */
.productInfo {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding-left: 25px;
}

.title {
  font-size: 2.4rem;
  font-weight: 700;
  margin-bottom: 15px;
  color: #333;
}

.oldPrice {
  text-decoration: line-through;
  color: #888;
  font-size: 1.2rem;
  margin-bottom: 10px;
}

.newPrice {
  color: #4caf50;
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 10px;
}

.description {
  font-size: 1.1rem;
  color: #555;
  line-height: 1.6;
  margin-bottom: 30px;
  font-weight: 400;
}

/* Option Group Styles */
.optionGroup {
  margin-bottom: 15px;
}

label {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 8px;
  font-weight: 600;
}

select,
.colorButton,
.sizeButton {
  font-size: 1rem;
  border-radius: 8px;
  transition: all 0.3s ease;
}

/* Select Dropdown Styles */
select {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  background-color: #f9f9f9;
}

select:hover,
.select:focus {
  border-color: #4caf50;
}

/* Color Button Styles */
.colorOptions {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}

.colorButton {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  border: 3px solid #ccc;
  cursor: pointer;
  transition: border-color 0.3s ease, transform 0.3s ease;
}

.colorButton.selectedColor {
  border-color: #000;
  transform: scale(1.1);
}

.colorButton:hover {
  border-color: #544ddc;
}

/* Size Button Styles */
.sizeOptions {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}

.sizeButton {
  padding: 12px 25px;
  background: #f4f4f4;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.3s ease;
}

.sizeButton.selectedSize {
  border: 2px solid #000;
  color: #000;
  transform: scale(1.05);
}

.sizeButton:hover {
  background: #ddd;
  transform: scale(1.05);
}

/* Add Button Styles */
.addButton {
  box-sizing: border-box;
  background-color: #544ddc;
  color: white;
  width: 150px;
  height: 50px;
  font-size: 1rem;
  padding: 6px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  margin-bottom: 12px;
}

/* Expanded Image Overlay */
.expandedImageOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.expandedImage {
  max-width: 85%;
  max-height: 85%;
  object-fit: contain;
  cursor: pointer;
  border-radius: 12px;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  .productDetail {
    grid-template-columns: 1fr;
    padding: 20px;
  }

  .productInfo {
    padding-left: 0;
  }

  .title {
    font-size: 2rem;
  }

  .oldPrice,
  .newPrice {
    font-size: 1.2rem;
  }

  .description {
    font-size: 1rem;
  }

  .mainImage {
    max-width: 100%;
  }

  .imageGrid {
    flex-wrap: wrap;
    gap: 10px;
  }

  .gridImage {
    width: 48%;
  }

  .colorOptions,
  .sizeOptions {
    flex-direction: row;
    gap: 12px;
    justify-content: center;
  }

  .addButton {
    box-sizing: border-box;
    width: 150px;
    height: 50px;
    font-size: 1rem;
    padding: 6px;
  }
}

/* Extra Small Screens (Mobile) */
@media screen and (max-width: 480px) {
  .container {
    padding: 30px 10px;
  }

  .productDetail {
    padding: 15px;
  }

  .productImages {
    gap: 10px;
  }

  .mainImage {
    max-width: 100%;
  }

  .gridImage {
    width: 48%;
  }

  .colorOptions,
  .sizeOptions {
    flex-direction: row;
    gap: 8px;
  }

  .addButton {
    box-sizing: border-box;
    width: 150px;
    height: 50px;
    font-size: 1rem;
    padding: 4px;
  }
}
