.container {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  justify-content: center;
  padding: 3rem 1.5rem;
}

.innerContainer {
  max-width: 24rem;
  margin: 0 auto;
  background: #ffffff;
  width: 20rem;
  padding: 5px 25px;
  border-radius: 5px;
}

.logo {
  display: block;
  margin: 0 auto;
  height: 2.5rem;
  width: auto;
}

.title {
  margin-top: 2.5rem;
  text-align: center;
  font-size: 1.4rem;
  font-weight: bold;
  color: #1f2937;
}

.form {
  margin-top: 2.5rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.label {
  display: block;
  font-size: 0.875rem;
  font-weight: 500;
  color: #1f2937;
}

.input {
  width: 100%;
  padding: 0.5rem;
  border-radius: 0.375rem;
  border: 1px solid #d1d5db;
  font-size: 0.875rem;
  color: #1f2937;
}

.input:focus {
  outline: none;
  border-color: #6366f1;
  box-shadow: 0 0 0 2px rgba(99, 102, 241, 0.5);
}

.error {
  color: #ef4444;
  font-size: 0.875rem;
}

.button {
  width: 100%;
  padding: 0.5rem;
  border-radius: 0.375rem;
  background-color: #4f46e5;
  color: white;
  font-size: 0.875rem;
  font-weight: 600;
  cursor: pointer;
  border: none;
  transition: background-color 0.2s;
}

.button:hover {
  background-color: #4338ca;
}

.linkContainer {
  margin-top: 2.5rem;
  text-align: center;
  font-size: 0.875rem;
  color: #6b7280;
}

.link {
  font-weight: 600;
  color: #4f46e5;
  text-decoration: none;
}

.link:hover {
  color: #4338ca;
}
