.paginationContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-top: 1px solid #d5d6d8;
  border-radius: 5px;
  margin-top: 15px;
}

.desktopPagination {
  display: flex;
  justify-content: space-between;
}

.pageInfo {
  font-size: 0.875rem;
  color: #4b5563;
  margin-top: 18px;
}

.pageNumber {
  font-weight: 600;
}

.navContainer {
  display: flex;
}

.pageNavButton {
  display: flex;
  align-items: center;
  padding: 0.5rem;
  font-size: 0.875rem;
  color: #4b5563;
  border: 1px solid #d1d5db;
  cursor: pointer;
  transition: background-color 0.2s ease;
  margin: 12px 2px;
  border-radius: 10px;
}

.pageNavButton:hover {
  background-color: #f3f4f6;
}

.activePage {
  background-color: #6366f1;
  color: #fff;
  border-color: #6366f1;
}

.inactivePage {
  color: #9ca3af;
}

.icon {
  height: 1.25rem;
  width: 1.25rem;
}
