.main {
  display: grid;
  min-height: 100vh;
  place-items: center;
  background-color: white;
  padding: 6rem 1.5rem; /* 24px to 32px on larger screens */
}

.textCenter {
  text-align: center;
}

.orderStatus {
  font-size: 1rem;
  font-weight: 600;
  color: #4c51bf; /* Indigo color */
}

.orderNumber {
  margin-top: 1rem;
  font-size: 2rem;
  font-weight: 700;
  color: #1a202c; /* Gray color */
}

.orderMessage {
  margin-top: 1.5rem;
  font-size: 1rem;
  line-height: 1.75;
  color: #4a5568; /* Gray color */
}

.buttonContainer {
  margin-top: 2.5rem;
  display: flex;
  justify-content: center;
  gap: 1.5rem;
}

.goHomeButton {
  background-color: #4c51bf; /* Indigo color */
  padding: 0.625rem 1.25rem;
  font-size: 0.875rem;
  font-weight: 600;
  color: white;
  text-decoration: none;
  border-radius: 0.375rem;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  transition: background-color 0.2s ease-in-out;
}

.goHomeButton:hover {
  background-color: #434190; /* Darker indigo */
}

.goHomeButton:focus-visible {
  outline: 2px solid #4c51bf;
  outline-offset: 2px;
}



@media screen and (max-width: 768px) {
  .main {
  }
  .textCenter{
    width: 65vw;
  }
  .orderNumber{
    font-size: 1.2rem;
  }

}
