.footerContainer {
  background-color: #111827;
  padding: 40px 20px;
  color: white;
  text-align: center;
}

.contentWrapper {
  max-width: 640px;
  margin: 0 auto;
}

.textCenter {
  margin-bottom: 40px;
}

.heading {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.downloadContainer {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-top: 20px;
}

.downloadButton {
  display: flex;
  align-items: center;
  border: 1px solid white;
  border-radius: 8px;
  padding: 8px 16px;
  width: 180px;
  cursor: pointer;
}

.downloadIcon {
  width: 28px;
  height: 28px;
}

.downloadText {
  margin-left: 12px;
  text-align: left;
}

.smallText {
  font-size: 12px;
  color: #d1d5db;
}

.largeText {
  font-size: 14px;
  font-weight: 500;
}

.footerBottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 14px;
  color: #9ca3af;
  margin-top: 50px;
}

.links {
  display: flex;
  gap: 16px;
  margin-top: 8px;
}

.links span {
  cursor: pointer;
  position: relative;
  padding-left: 8px;
}

.links span:not(:first-child)::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  height: 12px;
  width: 1px;
  background-color: #9ca3af;
  transform: translateY(-50%);
}
