/* Modal.module.css */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.modalContainer {
  width: 22rem;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1001;
}

.modalContent {
  background-color: white;
  padding: 1.25rem 1rem 1rem 1rem; /* sm:p-6 sm:pb-4 */
  border-radius: 0.375rem; /* sm:rounded-lg */
  width: 100%;
  max-width: 24rem; /* sm:max-w-lg */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* shadow-xl */
}

.header {
  display: flex;
  align-items: center;
  margin-bottom: 1.25rem; /* sm:mb-4 */
}

.iconContainer {
  width: 3rem; /* sm:h-10 sm:w-10 */
  height: 3rem;
  background-color: rgba(248, 113, 113, 0.2); /* bg-red-100 */
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 1rem; /* sm:mr-4 */
}

.icon {
  height: 1.5rem; /* h-6 w-6 */
  width: 1.5rem;
  fill: #dc2626; /* text-red-600 */
}

.title {
  font-size: 1rem; /* text-base */
  font-weight: 600; /* font-semibold */
  line-height: 1.5; /* leading-6 */
  color: #1f2937; /* text-gray-900 */
}

.message {
  font-size: 0.875rem; /* text-sm */
  color: #6b7280; /* text-gray-500 */
}

.buttonContainer {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  margin-top: 1rem; /* sm:mt-4 */
}

.dangerButton {
  background-color: #ef4444; /* bg-red-600 */
  color: white;
  border: none;
  padding: 0.625rem 1.25rem; /* px-3 py-2 */
  font-size: 0.875rem; /* text-sm */
  font-weight: 600; /* font-semibold */
  border-radius: 0.375rem; /* rounded-md */
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.dangerButton:hover {
  background-color: #dc2626; /* hover:bg-red-500 */
}

.cancelButton {
  background-color: white;
  color: #1f2937; /* text-gray-900 */
  border: 1px solid #d1d5db; /* ring-1 ring-inset ring-gray-300 */
  padding: 0.625rem 1.25rem; /* px-3 py-2 */
  font-size: 0.875rem; /* text-sm */
  font-weight: 600; /* font-semibold */
  border-radius: 0.375rem; /* rounded-md */
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.cancelButton:hover {
  background-color: #f3f4f6; /* hover:bg-gray-50 */
}
