.mainDiv {
  display: block;
  width: 70vw;
  margin: auto;
  padding: 20px;
}

.mainSection {
  background: #fff;
  width: 100%; /* Full width of the container */
  max-width: 800px; /* Prevents it from being too wide on larger screens */
  padding: 20px;
  border-radius: 8px;
  display: block;
  margin: 20px auto;
}

.formGroup {
  display: block;
  width: 80%; /* Full width of the form */
  margin: auto;
}

.label {
  display: block;
  font-weight: 600;
  font-size: 16px;
  margin-bottom: 8px;
  color: #333;
}

.input,
.textarea,
.select {
  width: 100%; /* Full width of the container */
  padding: 12px;
  margin-top: 8px;
  border-radius: 6px;
  border: 1px solid #ccc;
  background-color: #f9f9f9;
  transition: border-color 0.3s ease;
}

.input:focus,
.textarea:focus,
.select:focus {
  border-color: #007bff;
  outline: none;
  background-color: #fff;
}

.textarea {
  resize: vertical;
}

/* Helper Text */
.helperText {
  font-size: 12px;
  color: #6b7280;
  margin-top: 5px;
  max-width: 400px;
}

.inputGroup {
  margin-bottom: 25px;
}

.inputLabel {
  font-size: 14px;
  margin-bottom: 5px;
  color: #333;
}

.inputField {
  width: 100%;
  padding: 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
  background-color: #f9f9f9;
  transition: border-color 0.3s ease;
}

.inputField:focus {
  border-color: #007bff;
  outline: none;
}

.inputField::placeholder {
  color: #aaa;
}

.inputField:focus::placeholder {
  color: #888;
}

.section {
  display: block;
  width: 100%;
  margin: auto;
  margin-top: 30px;
  max-width: 500px; /* Limit the width */
}

.sectionTitle {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 15px;
  color: #111827;
}

.checkboxGroup {
  margin-top: 15px;
}

.checkboxItems {
  display: flex;
  flex-direction: column;
}

.checkboxItem {
  margin-bottom: 15px;
  display: flex;
  align-items: center;
}

.checkboxWrapper {
  margin-right: 10px;
}

.checkbox {
  width: 20px;
  height: 20px;
  border-radius: 0.25rem;
  border: 1px solid #d1d5db;
  background-color: transparent;
  transition: border-color 0.3s ease;
}

.checkbox:focus {
  border-color: #4f46e5;
  outline: none;
}

.checkboxLabel {
  font-size: 14px;
  color: #333;
}

.checkboxDescription {
  font-size: 12px;
  color: #6b7280;
  margin-top: 5px;
}

.buttonGroup {
  display: flex;
  justify-content: center; /* Center buttons horizontally */
  gap: 20px;
  margin-top: 25px;
  width: 100%;
}

.cancelButton,
.deleteButton,
.saveButton {
  padding: 12px 20px;
  font-size: 14px;
  font-weight: 600;
  border-radius: 6px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: background-color 0.3s ease, border-color 0.3s ease;
}

.cancelButton {
  background-color: #f3f4f6;
  color: #1f2937;
  border: 1px solid #d1d5db;
}

.cancelButton:hover {
  background-color: #e5e7eb;
}

.deleteButton {
  background-color: #ef4444;
  color: white;
  border: 1px solid #dc2626;
}

.deleteButton:hover {
  background-color: #dc2626;
}

.saveButton {
  background-color: #3b82f6;
  color: white;
  border: 1px solid #2563eb;
}

.saveButton:hover {
  background-color: #2563eb;
}

.imgInputGroup {
  display: block;
  width: 80%;
  margin: auto;
}
