/* General Styling */
.container {
  padding: 20px;
  border-radius: 10px;
  max-width: 900px;
  margin: 30px auto;
  font-family: "Roboto", sans-serif;
  width: 50vw;
  display: block;
  margin: 25px auto;
  border: 1px solid #e2e1f5;
}

/* Title */
.title {
  font-size: 2.2rem;
  color: #333;
  font-weight: 600;
  margin-bottom: 15px;
}

.subtitle {
  font-size: 1.1rem;
  color: #555;
  margin-bottom: 15px;
}

.role {
  font-size: 1rem;
  color: #4caf50;
  font-weight: 500;
}

/* Button Styling */
.button,
.submitButton,
.editButton,
.removeButton,
.cancelButton {
  background-color: #4f46e5;
  color: white;
  padding: 12px 25px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-weight: 500;
  margin: 15px;
}

/* Form Styling */
.form {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;
  border: 1px solid #b4b1f3;
}

.formTitle {
  font-size: 1.6rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 12px;
}

.formDescription {
  font-size: 1rem;
  color: #777;
  margin-bottom: 20px;
}

.formGrid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 20px;
  margin-top: 15px;
}

.formField {
  display: flex;
  flex-direction: column;
}

.label {
  font-size: 1rem;
  color: #444;
  margin-bottom: 10px;
  font-weight: 500;
}

.input {
  padding: 12px;
  font-size: 1rem;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
  transition: border-color 0.3s ease;
}

.input:focus {
  border-color: #4f46e5;
  outline: none;
}

.error {
  color: red;
  font-size: 0.9rem;
  margin-top: 5px;
}

/* Address container */
.addressContainer {
  background-color: #fff;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
}

.addressDetails {
  margin-top: 15px;
}

.addressDetails p {
  margin: 4px 0;
  color: #444;
}

.addressButtons {
  margin-top: 15px;
  display: flex;
  gap: 10px;
}

.editButton,
.removeButton {
  padding: 10px 22px;
  font-size: 0.9rem;
  font-weight: 600;
  margin: 5px;
}

.editButton {
  background-color: #4f46e5;
}

.removeButton {
  background-color: #4f46e5;
}

/* Responsive Design */
@media (max-width: 768px) {
  .container {
    padding: 10px;
    width: 90vw;
  }

  .title {
    font-size: 2rem;
  }

  .formGrid {
    grid-template-columns: 1fr;
  }

 
}

/* Address section */
.addressSection {
  margin-top: 40px auto;
  box-sizing: border-box;
  border-radius: 5px;
  width: 98%;
  display: block;

}


.addressContainer {
  margin-bottom: 15px;
  border-radius: 10px;
  width: 98%;
  box-sizing: border-box;
  display: block;
  margin: auto;

}

.addressDetails {
  background: #ffffff;
}

.addressButtons button {
  padding: 10px 20px;
  font-size: 0.9rem;
  font-weight: 600;
}


.userInfo{
  background: #ffffff;
  padding: 8px;
  border-radius: 5px;
}

.title {
  font-size: 1.3rem;
}