.addProductButton {
  box-sizing: border-box;
  text-align: center;
  display: inline-block;
  background-color: #5f74d1;
  color: #ffffff;
  font-weight: bold;
  text-decoration: none;
  border-radius: 5px;
  padding: 6px;
  height: 40px;
  width: 10rem;
  border: none;
}

.productInfo h3 {
  font-size: 1rem;
}

.addProductButton:hover {
  background-color: #4161ed;
}

.productImage {
  border-radius: 8px;
}

.productImage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.productRating span {
  color: #ff9800;
}

.productPrice {
  display: flex;
  justify-content: space-between;
}

.discountedPrice {
  color: #4caf50;
}

.originalPrice {
  text-decoration: line-through;
  color: #999;
}

.editButton {
  display: block;
  width: 90%;
  margin: auto;
  background-color: #3f51b5;
  color: white;
  padding: 8px 10px;
  text-decoration: none;
  border-radius: 5px;
  text-align: center;
  margin-top: 20px;
}

.editButton:hover {
  background-color: #303f9f;
}

.productLink {
  text-decoration: none;
  color: #333;
}

/* General Container */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* Header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  border-bottom: 2px solid #eee;
}

.header h1 {
  font-size: 1.8rem;
  font-weight: 600;
}

.toggleButtons {
  display: flex;
  gap: 10px;
}

/* Toggle Button Styles */
.toggleButton {
  padding: 8px 20px;
  background: #4f46e5;
  cursor: pointer;
  margin: 5px;
  color: #fff;
  border-radius: 18px;
  border: none;
}

/* Product Card */
.productCard {
  padding: 16px;
  border: 1px solid #f3f1f1;
  border-radius: 8px;
  transition: transform 0.3s ease-in;
  cursor: pointer;
  background: #ffffff;
  width: 270px;
  margin: 15px 10px;
  box-sizing: border-box;
  display: inline-block;
}

.productCard:hover {
  background: #ddd;
}

/* Product Info */
.productInfo {
  margin-top: 10px;
}

.strikeThrough {
  text-decoration: line-through;
  color: #888;
}

/* Modal */
.modal {
  position: fixed;
  top: 0;
  left: -100%;
  width: 250px;
  height: 100%;
  background-color: #fff;
  box-shadow: 4px 0px 10px rgba(0, 0, 0, 0.1);
  transition: left 0.3s ease-in-out;
  z-index: 999;
}

.modal.open {
  left: 0;
}

.modalContent {
  padding: 20px;
}

/* Close Modal Button */
.closeModal {
  background-color: transparent;
  color: #333;
  font-size: 1.5rem;
  font-weight: bold;
  border: none;
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 8px 12px;
  border-radius: 50%;
  transition: background-color 0.2s, color 0.2s;
  outline: none;
}

.closeModal:hover {
  background-color: #f4f4f4;
  color: #4f46e5;
}

.closeModal:active {
  transform: scale(0.98);
}

/* Filter & Sort */
.filters,
.sort {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.filterOption,
.sort button {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  transition: background-color 0.3s;
}

/* Custom Checkbox */
.filterOption input[type="checkbox"] {
  appearance: none;
  width: 20px;
  height: 20px;
  border: 2px solid #ddd;
  border-radius: 50%;
  background-color: white;
  transition: all 0.3s ease;
  position: relative;
}

.filterOption input[type="checkbox"]:checked {
  background-color: #4f46e5;
  border-color: #4f46e5;
}

.filterOption input[type="checkbox"]:checked::before {
  content: "✔";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 12px;
}

.filterOption input[type="checkbox"]:hover {
  border-color: #4f46e5;
}

/* Sort Button */
.sort button {
  padding: 10px 20px;
  background-color: white;
  border: 2px solid #ddd;
  border-radius: 50px;
  font-size: 1rem;
  font-weight: 500;
  min-width: 160px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.sort button:hover {
  border-color: #4f46e5;
  background-color: #f4f4f4;
}

.sort button.selected {
  background-color: #4f46e5;
  color: white;
  border-color: #4f46e5;
}

.sort button::before {
  content: "✔";
  position: absolute;
  left: 35px;
  font-size: 16px;
  color: #fff;
  /* color: red; */
  display: none;
}

.sort button.selected::before {
  display: inline-block;
}

/* Pagination */
.pagination {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
}

.pagination button {
  background-color: #4f46e5;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.pagination button:hover {
  background-color: #4338ca;
}

/* Mobile Responsive Grid */
@media (max-width: 768px) {
  .container {
    padding: 10px;
  }

  .header h1 {
    font-size: 1.2rem;
  }

  .toggleButtons {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  .productCard {
    width: 95%;
    margin: 10px 0;
  }

  .addProductButton {
    width: 95%;
    font-size: 1rem;
  }

  .editButton {
    width: 100%;
  }

  .productInfo h3 {
    font-size: 0.9rem;
  }
}

/* Adjustments for smaller screens */
@media (max-width: 480px) {
  .header h1 {
    font-size: 1.2rem;
    margin: 5px;
  }

  .toggleButton {
    padding: 6px 15px;
    font-size: 0.9rem;
  }

  .modal {
    width: 60%;
  }

  .modalContent {
    padding: 15px;
  }

  .pagination {
    flex-wrap: wrap;
    gap: 5px;
  }

  .pagination button {
    padding: 8px 12px;
    font-size: 0.9rem;
  }
}

